.nav {
    font-size: 22px;
    grid-area: nav;

    text-align: center;
    background-color: rgb(7, 143, 255);

    border: 3px solid #000;
    border-radius: 5px;
}

.normal {
    color: #000;
    text-decoration: none;
}

.active {
    color: #ffffff;
}

.nav div {
    margin: 5px 0;
}